import Vue from "vue";
import {
  Pagination,
  Button,
  Spin,
  Icon,
  List,
  Timeline,
  Table,
  Tabs,
  Cascader,
  Select,
  DatePicker,
  Statistic,
  Carousel,
  ConfigProvider,
} from "ant-design-vue";

(function addComponents(Components) {
  for (let Component of Components) {
    Vue.component(Component.name, Component);
  }
})([
  Pagination,
  Button,
  Spin,
  Icon,
  List,
  List.Item,
  Timeline,
  Timeline.Item,
  Table,
  Tabs,
  Tabs.TabPane,
  Cascader,
  Select,
  Select.Option,
  DatePicker,
  Statistic,
  Carousel,
  ConfigProvider,
]);
