<template>
  <div class="menus-item" :class="{active}" @click="$emit('click')">
    <div class="menus-icon">
      <img :src="src" :alt="title" />
    </div>
    <span>{{title}}</span>
  </div>
</template>

<script>
export default {
  props: ["title", "active", "icon"],
  computed: {
    src() {
      return require("@/assets/menus/" + this.icon + ".png");
    }
  }
};
</script>

<style>
.menus-item {
  text-align: center;
  cursor: pointer;
}

.menus-item + .menus-item {
  margin-left: 34px;
}

.menus-icon {
  width: 84px;
  height: 84px;
  background-image: url("../../assets/menu.png");
  background-size: cover;
  margin-bottom: 14px;
}

.menus-icon img {
  width: auto;
  margin-top: 18px;
  height: 48px;
}

.menus-item span {
  width: 53px;
  height: 12px;
  font-size: 13px;
  font-family: Adobe Heiti Std;
  font-weight: normal;
  color: rgba(96, 198, 255, 1);
}

.active.menus-item .menus-icon,
.menus-item:hover .menus-icon {
  background-image: url("../../assets/menu_active.png");
}

.active.menus-item span,
.menus-item:hover span {
  color: rgba(0, 255, 228, 1);
}
</style>