export function apiHelper(Vue) {
  Vue.prototype.$getData = function(path, method = "POST") {
    return new Promise((resolve) => {
      axios({
        method,
        url: path,
      }).then((res) => {
        if (res.status === 200) {
          let { code, data } = res.data;

          if (!code) {
            resolve(data);
          }
        }

        // throw
      });
    });
  };
}
