import Vue from "vue";
import VueRouter from "vue-router";

Vue.use(VueRouter);

const routes = [
  {
    ...routeGenerator("trade", "supervision"),
    path: "/",
  },
  ...routerGroup("emergency", ["dictate", "event"]),
  ...routerGroup("operations", [
    "behavior",
    "flow",
    "history",
    "holiday",
    "market",
    "propaganda",
    "sms",
  ]),
  ...routerGroup("services", ["projects", "resources", "tourism"]),
  ...routerGroup("supervision", [
    "analysis",
    "monitor",
    "public",
    "suggest",
    "trade",
  ]),
];

function routerGroup(group, names) {
  return names.map((name) => routeGenerator(name, group));
}

/**
 *
 * @param name e.g. hello-world
 */
function routeGenerator(name, group) {
  let component =
    (group ? `${group}/` : "") +
    name
      .split("-")
      .map((str) => `${str.charAt(0).toUpperCase()}${str.slice(1)}`)
      .join("") +
    ".vue";

  return {
    path: `/${name}`,
    name,
    meta: {
      group,
    },
    component: () =>
      import(
        /* webpackChunkName: "[request]" */
        `../views/${component}`
      ),
  };
}

const router = new VueRouter({
  routes,
});

export default router;

router.beforeEach((_to, _from, next) => {
  setTimeout(() => {
    router.app.$store.commit("setLoading", true);
  }, 0);

  next();
});

router.afterEach(() => {
  setTimeout(() => {
    router.app.$store.commit("setLoading", false);
  }, 300);
});
