import Vue from "vue";
import App from "./App.vue";
import router from "./router";
import store from "./store";
import { cardLocker } from "./utils";
import { apiHelper } from "./api";

import "./assets/theme";
import "./plugins/ant-design-vue";

Vue.config.productionTip = false;

cardLocker(Vue, store, router);
apiHelper(Vue, store, router);

new Vue({
  router,
  store,
  render: (h) => h(App),
}).$mount("#app");
