const LOCK_TIME_OUT = 6666;

export function cardLocker(Vue, store, router) {
  Vue.prototype.$lock = function() {
    let resolver;

    this.$store.commit("lock/lock", {
      id: this._uid,
      promise: new Promise((resolve) => (resolver = resolve)),
    });

    this.$lock_resolver = resolver;
  };

  Vue.prototype.$unlock = function({ monthSelect } = {}) {
    let vm = this;

    while (vm) {
      if (vm.$lock_resolver) {
        vm.$lock_resolver();

        if (monthSelect) {
          vm.$set(vm, "showMonth", true);
        }

        break;
      }

      vm = vm.$parent;
    }
  };

  router.beforeEach((_to, _from, next) => {
    store.commit("lock/unlock");
    next();
  });

  store.registerModule("lock", {
    state: {
      lockingList: [],
    },
    mutations: {
      lock(state, payload) {
        state.lockingList.push(payload);

        checkProgress(state);
      },
      unlock(state, _id) {
        state.lockingList = state.lockingList.filter(
          ({ id }) => typeof _id === "number" && id !== _id
        );
      },
    },
    namespaced: true,
  });

  function checkProgress(state) {
    if (state._lock_checking) {
      return;
    }

    state._lock_checking = true;

    setTimeout(async () => {
      for (let { id, promise } of state.lockingList) {
        if (state.lockingList[0] && state.lockingList[0].id !== id) {
          state._lock_checking = false;
          checkProgress(state);
          return;
        }

        store.commit("lock/unlock", id);

        await Promise.race([
          promise,
          new Promise((resolve) => setTimeout(resolve, LOCK_TIME_OUT)),
        ]);

        await new Promise(requestAnimationFrame);
      }

      state._lock_checking = false;
    });
  }
}
