<template>
  <div class="tabs-item" :class="{active}" @click="$emit('click')">
    <i class="tabs-background"></i>
    <span>{{title}}</span>
  </div>
</template>

<script>
export default {
  props: ["title", "active"]
};
</script>

<style>
.tabs-item {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 124px;
  height: 40px;
  font-size: 20px;
  font-family: Adobe Heiti Std;
  font-weight: normal;
  color: rgba(255, 255, 255, 1);
  line-height: 20px;
  cursor: pointer;
}

.tabs-background {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    0deg,
    rgba(5, 52, 94, 1),
    rgba(1, 9, 16, 1),
    rgba(1, 59, 96, 1)
  );
  border: 2px solid;
  border-image: linear-gradient(
      0deg,
      rgba(51, 171, 255, 1),
      rgba(3, 114, 204, 1)
    )
    2 2;
  opacity: 0.59;
}

.tabs-item span {
  transform: translate(0, 0);
}

.active .tabs-background,
.tabs-item:hover .tabs-background {
